import React from "react";
import { Box, Heading, Stack } from "@chakra-ui/react";
import { Link as RLink } from "react-router-dom";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "../css/Film.css";
import "react-lazy-load-image-component/src/effects/blur.css";

interface IFilm {
	kinopoiskId: number | undefined;
	filmId: number | undefined;
	nameRu: any;
	nameEn: any;
	year?: number;
	posterUrl: string;
	posterUrlPreview: string;
	countries?: any;
	genres?: any;
	duration?: number | null | undefined;
	premiereRu?: string | null | undefined;
}

function Film({ film, isSearch }: { film: IFilm; isSearch?: boolean }) {
	return (
		<Box
			minW={isSearch ? "full" : "140px"}
			w={isSearch ? "full" : "140px"}
			maxW={isSearch ? "full" : "140px"}
		>
			<Box as={RLink} to={`/film/${film.kinopoiskId || film.filmId}`}>
				<Stack direction="column" spacing="10px">
					<LazyLoadImage
						src={film.posterUrl}
						width={isSearch ? "auto" : "140px"}
						height={isSearch ? "auto" : "210px"}
						className="poster_image"
						effect="blur"
					/>
					<Heading
						size="sm"
						maxW={isSearch ? "full" : "140px"}
						overflow="hidden"
						textOverflow="ellipsis"
						whiteSpace={isSearch ? "normal" : "nowrap"}
					>
						{film.nameRu || film.nameEn}
					</Heading>
				</Stack>
			</Box>
		</Box>
	);
}

export default Film;
